.App {
  background-image: linear-gradient(
    -225deg,
    #473b7b 0%,
    #3584a7 51%,
    #30d2be 100%
  );
}
.card {
  width: 98%;
  display: flex;
  margin: 0;
  padding: 5px;
  align-items: center;
  padding-left: 1%;
  justify-content: left;
  border-top: 0.5px solid grey;
  border-width: 100%;
  line-height: 8px;
}
.card-email {
  color: white;
  font-style: none;
  font-size: 1em;
}
